import {ContentBuilder} from "./ui/popup/content-builder";
import FormElements from "./ui/form/elements";

export default class SimpleJSONContentBuilder implements ContentBuilder {

    build(data: any): any {
        const container:HTMLElement = document.createElement('form');
        container.classList.add('form');

        container.setAttribute('method', data.method);
        container.setAttribute('action', data.action);

        for (var i = 0; i < data.items.length; i++) {
            const item = data.items[i];

            const element = FormElements.text(item.label, item.name, item.value, item.placeholder);
            container.appendChild(element);
        }

        return container;
    }

}